.App {
  text-align: center;
  background-color: rgba(26, 26, 26, 1);
}

.page-content {
  height: 100vh;
  width: 100vw;
  color: rgba(255, 239, 213, 1);
}

.page-header {
  font-size: 4em;
  font-weight: 100;
  text-transform: uppercase;
  border-bottom: dotted rgba(255, 80, 80, 0.5);
}

.navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  height: 8vh;
}

.nav-link {
  font-size: 2em;
  font-weight: 300;
  text-decoration: none;
  color: rgba(255, 239, 213, 1);
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  height: 8vh;
  background-color: rgba(26, 26, 26, 1);
}

.socials {
  color: rgba(255, 239, 213, 1);
}

.home-content {
  position: relative;
  top: 20vmin;
}
